*,
*::after,
*::before {
  box-sizing: inherit;
}

html {
  font-size: 62.5%;
}

body {
  font-family: "Lato", sans-serif;
  box-sizing: border-box;
  margin: 0;
}

a {
  text-decoration: none;
}

ul {
  list-style: none;
  padding: 0;
  margin: 0;
}

img {
  display: block;
  width: 100%;
}

.container {
  margin: 0 auto;
  max-width: 120rem;
  width: 80%;
}

.section__header {
  text-align: center;
  margin: 5rem 0;
}

.heading-h2 {
  font-size: clamp(2.6rem, 5vw, 4rem);
}

.p-line {
  font-size: clamp(1.8rem, 1vw, 2rem);
}

@media (width > 60em) {
  .col-2 {
    flex: 0 1 calc(50% - 2rem);
  }
}
@media (width > 70em) {
  .col-2 {
    flex: 0 1 calc(50% - 5rem);
  }
}

@media (width > 60em) {
  .col-3 {
    flex: 0 1 calc(33.3333333333% - 1.5rem);
  }
}

@media (width > 60em) {
  .col-4,
  .footer-widget {
    flex: 0 1 calc(50% - 6rem);
  }
}
@media (width > 70em) {
  .col-4,
  .footer-widget {
    flex: 0 1 calc(25% - 6rem);
  }
}

.btn {
  padding: 1rem 3rem;
  border-radius: 0.652rem;
  text-transform: uppercase;
  color: #fff;
  font-size: 1.4rem;
}

.btn--bg--white {
  background: #fff;
}

.btn--text--colored {
  color: #ed4c5c;
}

.featured__post {
  position: relative;
  font-size: 1.6rem;
  margin-bottom: 14rem;
  top: -7rem;
  transform: translateY(8rem);
  transition: all 1s ease;
}
.featured__post img {
  border-radius: 1rem;
}
.featured__post .text-content {
  position: absolute;
  background: #fff;
  left: 0;
  right: 0;
  bottom: -10rem;
  margin-left: auto;
  margin-right: auto;
  width: 80%;
  padding: 2rem;
  padding-top: 1rem;
  text-align: center;
  border-radius: 1rem;
  box-shadow: 0px 5px 30px rgba(0, 0, 0, 0.1);
}
.featured__post .link {
  text-transform: capitalize;
  color: #ed4c5c;
  display: block;
  font-size: 1.6rem;
}

.technology__item {
  font-size: clamp(1.8rem, 1vw, 2rem);
  text-align: center;
  margin-top: 8rem;
  position: relative;
}
.technology__item .fas {
  color: #ed4c5c;
  font-size: 5rem;
}
.technology__item:nth-child(1):before {
  content: "01";
}
.technology__item:nth-child(2):before {
  content: "02";
}
.technology__item:nth-child(3):before {
  content: "03";
}
.technology__item:nth-child(4):before {
  content: "04";
}
.technology__item:nth-child(1):before, .technology__item:nth-child(2):before, .technology__item:nth-child(3):before, .technology__item:nth-child(4):before {
  font-size: 10rem;
  font-weight: bold;
  color: rgba(16, 16, 16, 0.08);
  position: absolute;
  left: 0;
  right: 0;
  top: -6.8rem;
  z-index: -1;
}

.contact__form .form input,
.contact__form .form select,
.contact__form .form option {
  color: #132743;
}
.contact__form .form input,
.contact__form .form select {
  width: 100%;
  padding: 2rem 1.8rem 2rem;
  border-radius: 1rem;
  border: 1px solid #333333;
  outline: none;
}
.contact__form .form select {
  appearance: none;
  background: url(http://cdn1.iconfinder.com/data/icons/cc_mono_icon_set/blacks/16x16/br_down.png) no-repeat right;
  background-position-x: 97%;
}
.contact__form .form__col-2-input {
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
}
.contact__form .form__col-2-input .col {
  flex: 0 1 48%;
}
.contact__form .form__input {
  margin-bottom: 2rem;
}
.contact__form .form__btn {
  cursor: pointer;
  display: block;
  background: #ed4c5c;
  border: none;
  padding: 1.2rem 3rem;
}

.header {
  background-image: linear-gradient(90deg, rgba(213, 32, 71, 0.8) 0%, rgba(238, 61, 27, 0.8) 100%), url(/img/hero.jpg);
  background-size: cover;
}
.header__text {
  text-align: center;
  color: #fff;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  padding-block: 15%;
}
.header__text h1 {
  font-size: clamp(3rem, 7vw, 8rem);
  text-transform: uppercase;
  transition: all 250ms ease;
  margin: 0;
}
.header__text p {
  max-width: 52rem;
  font-size: clamp(1.6rem, 2vw, 2rem);
}

.navbar {
  font-size: 1.4rem;
  color: #fff;
  display: flex;
  flex-wrap: wrap;
  padding-top: 5rem;
  padding-bottom: 1.7rem;
  transition: transform 1s;
  justify-content: space-between;
  align-items: center;
}
.navbar__logo a {
  font-size: 1.6rem;
  color: #fff;
  display: flex;
  align-items: center;
  justify-content: center;
}
.navbar__logo img {
  width: 3rem;
}
.navbar__logo p {
  text-transform: uppercase;
  font-weight: 700;
  margin-left: 0.652rem;
}
.navbar__menu {
  font-weight: 400;
  display: none;
  align-items: center;
  justify-content: center;
}
@media (width > 70em) {
  .navbar__menu {
    display: flex;
  }
}
.navbar__menu.openMenuMobile {
  background: rgba(19, 19, 19, 0.8);
  transform: translateY(0);
  opacity: 1;
  transition: all 0.5s ease;
}
.navbar__menu li {
  margin: 1rem;
}
.navbar__menu li a {
  color: #fff;
  text-transform: uppercase;
}
@media (width > 70em) {
  .navbar__menu li:nth-child(6) {
    display: none;
  }
}
.navbar__location {
  display: none;
  font-weight: 400;
}
@media (width > 70em) {
  .navbar__location {
    display: flex;
  }
}
.navbar__location a {
  color: #fff;
  text-transform: uppercase;
}
.navbar__hamburger {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  height: 1.9rem;
  cursor: pointer;
}
@media (width > 70em) {
  .navbar__hamburger {
    display: none;
  }
}
.navbar__hamburger span {
  width: 2rem;
  height: 0.3rem;
  background: #fff;
}
.navbar__hamburger.active span:nth-child(1) {
  margin: 8px auto;
  transform: translateY(13px) rotate(45deg);
}
.navbar__hamburger.active span:nth-child(2) {
  opacity: 0;
}
.navbar__hamburger.active span:nth-child(3) {
  margin: 8px auto;
  transform: translateY(-13px) rotate(-45deg);
}

.manage {
  padding: 8rem 0;
}
.manage .d-flex-layout {
  flex-wrap: wrap;
  justify-content: space-between;
  align-items: center;
}
@media (width > 60em) {
  .manage .d-flex-layout {
    display: flex;
  }
}
.manage__text {
  padding-bottom: 6rem;
}
.manage__video {
  position: relative;
}
.manage__video img {
  border-radius: 2rem;
}
.manage__video-play {
  position: absolute;
  font-size: 2rem;
  cursor: pointer;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
}
@media (width > 70em) {
  .manage__video-play {
    top: 50%;
    left: 0;
    transform: translate(-50%, -50%);
  }
}
.manage__video-play .fa-play {
  background: #ed4c5c;
  color: #fff;
  width: 8rem;
  height: 8rem;
  display: flex;
  align-items: center;
  justify-content: center;
  border-radius: 5rem;
}
.manage .link {
  text-transform: capitalize;
  color: #ed4c5c;
  display: block;
  font-size: clamp(1.8rem, 1vw, 2rem);
}

.featured {
  padding-block: 6rem;
  background: #fafafa;
}
.featured .d-flex-layout {
  flex-wrap: wrap;
  justify-content: space-between;
  align-items: center;
}
@media (width > 60em) {
  .featured .d-flex-layout {
    display: flex;
  }
}

.technology {
  padding-block: 5rem;
}
.technology .d-flex-layout {
  flex-wrap: wrap;
  justify-content: space-between;
  align-items: center;
}
@media (width > 60em) {
  .technology .d-flex-layout {
    display: flex;
  }
}

.app {
  padding: 8rem 0;
  background: #ed4c5c;
  color: #fff;
}
.app .d-flex-layout {
  flex-wrap: wrap;
  justify-content: space-between;
  align-items: center;
}
@media (width > 60em) {
  .app .d-flex-layout {
    display: flex;
  }
}
.app__text {
  padding-bottom: 6rem;
}
.app__social {
  font-size: clamp(1.8rem, 1vw, 2rem);
}
.app__social a {
  color: #fff;
  margin-right: 2rem;
}
.app__video-slider img {
  border-radius: 2rem;
}

.contact {
  padding: 8rem 0;
}
.contact .d-flex-layout {
  flex-wrap: wrap;
  justify-content: space-between;
  align-items: center;
}
@media (width > 60em) {
  .contact .d-flex-layout {
    display: flex;
  }
}
.contact__text {
  padding-bottom: 6rem;
}

.footer {
  background: #fafafa;
}
.footer__widgets {
  display: flex;
  justify-content: space-between;
  flex-wrap: wrap;
  font-size: clamp(1.8rem, 1vw, 2rem);
}
.footer__widget {
  margin: 0 1rem 1rem;
}
@media (width > 25em) {
  .footer__widget img {
    width: auto;
  }
}
.footer__widget-links li a {
  display: block;
  text-transform: capitalize;
  margin-bottom: 0.352rem;
}
.footer__widget-social {
  display: flex;
}
.footer__widget-social li {
  padding-right: 2rem;
}
.footer__widget-social li a {
  color: #101010;
  transition: all 250ms ease;
}
.footer__widget-social li a:hover {
  color: #ee3d1b;
}
.footer__creds {
  font-size: clamp(1.8rem, 1vw, 2rem);
  padding: 3rem 0;
  text-align: center;
  padding-top: 2rem;
}
@media (width > 70em) {
  .footer__creds {
    text-align: left;
  }
}
.footer__creds p {
  margin: 0;
}